body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#Results {
  position: relative;
  background-size: cover;
  background-attachment: fixed;
  clear: both;
  min-height: 300px;
  /* height: 1000px; */
  margin: 1% 0 5%;
  padding: 1% 7% 10%;
  text-align: left;
  border-radius: 4px;
}

.list-overflow-container {
  /* max-height: 545px; */
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: auto;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.list-group {
  height: auto;
  color: rgb(110, 156, 216);

}

.list-group-item {
  padding: 3% 0% 1% 3%
}

.list-group :hover{
  background-color: rgba(255, 255, 255, 0)
}
#form {
  text-align: center;
}

.input {
  width: 50%;
  margin-right: auto;
  margin-left: auto;
  border-radius: 25px;
  box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.3);
}

.btn {
  padding-right: 20px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

