.list-overflow-container {
  /* max-height: 545px; */
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: auto;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.list-group {
  height: auto;
  color: rgb(110, 156, 216);

}

.list-group-item {
  padding: 3% 0% 1% 3%
}

.list-group :hover{
  background-color: rgba(255, 255, 255, 0)
}